/*Estilos para Ritmo */
:root {
    --text-color: rgb(23, 23, 23);
    --contrast-color: rgb(222, 12, 23);
    --bg-dark-color: rgb(0, 0, 0);
    --bg-light-color: rgba(0, 0, 0, 0.1);
    --success-color: green;
    --error-color: red;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Merriweather', sans-serif;
    font-weight: 400;
    color: var(--text-color);
    line-height: 1.5;
}

h1,
h2 {
    font-weight: 700;
}

p {
    font-size: 1.3rem;
    margin-bottom: 1rem;
}

a {
    color: var(--text-color);
    text-decoration: none;
    border-bottom: 1px dotted var(--text-color);
}

a:hover {
    color: var(--contrast-color);
    border-bottom: 1px dotted var(--contrast-color);
}

cite {
    font-style: normal;
}

abbr {
    text-decoration: none;
}

h1 {
    font-size: 2.2rem;
}

main h1 {

    padding-bottom: 20px;
}

h2 {
    font-size: 1.7rem;
    margin-bottom: 1.5rem;
}

h3 {
    font-size: 1.75rem;
    margin-bottom: 1rem;
}

ul {
    padding-left: 80px;
    list-style-type: square;
}

main ul {

    padding-top: 30px;
}

ul li {
    margin-bottom: 1rem;
    font-size: 1.1rem;
}


header {
    height: 12vh;
    padding-top: 25px;
    padding-bottom: 15px;
    background: var(--bg-dark-color);
    min-height: 180px;
}

footer {
    height: 15vh;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: var(--bg-dark-color);
    color: #fff;

}

main {
    padding-top: 50px;
    padding-bottom: 50px;
}

/*generico*/
.container {
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
    width: 100%;
}

.containerCentrado {

    text-align: center;
}

@media (min-width: 576px) {
    .container {
        max-width: 540px;
    }
}

@media (min-width: 576px) {
    header {
        min-height: 240px;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 720px;
    }
}

@media (min-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}

/****************************
Titular del sitio
*****************************/

h1.banner {
    color: white;
}

.AlignCenter {
    display: flex;
    justify-content: center;
}

h1.banner a {
    color: inherit;
    border: none;
}

/*************************
Contenidos del footer
**************************/
footer {
    font-size: 0.8rem;
    color: white;
}

footer p {
    font-size: inherit;
    margin-bottom: 0.5rem;
}

footer a {
    color: inherit;
    border: none;
}

footer a:hover {
    border: none;
}

footer ul {
    list-style-type: none;
    padding-left: 0;
}

footer ul li {
    display: inline-block;
    padding-right: 5px;
}

footer ul li::after {
    content: "·";
    padding-left: 10px;
}

footer ul li:last-child::after {
    content: "";
    padding-left: 10px;
}


/****************************
Menú de navegación del sitio
*****************************/
nav {
    text-align: left;
    color: white;
}

nav ul {
    padding-top: 20px;
    padding-left: 0;
}

nav ul li {
    display: inline-block;
    font-size: 1.15rem;
    font-weight: 400;
    padding-left: 2px;
}

nav ul li::before {
    content: "   ·   ";
    width: 10px;
}

nav ul li:first-child::before {
    content: "";
}

nav ul li a {
    color: inherit;
    border: none;
}

nav ul li a:hover {
    border: none;
    color: var(--contrast-color);
}

nav li.active,
footer li.active {
    font-weight: 700;
}

nav li.active::before {
    font-weight: 400;
}

/****************************
Artículo 
****************************/
article footer {
    height: auto;
    background-color: transparent;
    color: var(--text-color);
    font-size: 0.85rem;
}

main a {
    color: inherit;
    border-bottom: 1px dotted var(--text-color);
}

main a:hover {
    border-bottom: 1px dotted var(--contrast-color);
}


/*Esta regla permite que el tamaño de la letra de estos dos elementos de la página de inicio tengan un tamaño mayor cuando tengamos una pantalla de una anchura mínima de 1200 píxeles*/

@media (min-width: 1200px) {
    body.home h1 {
        font-size: 9rem;
    }

    body.home main p {
        font-size: 2.3rem;
    }
}

/****************************
Formulario
*****************************/
.required {
    font-size: 0.9rem;
    margin-bottom: 0;
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
}

form {
    width: 100%;
    font-size: 1.2rem;
}

fieldset {
    padding: 2rem;
    border: 1px solid #000;
    margin: 15px 0;
}

.outFieldset {
    padding: 2rem;
}

legend {
    font-weight: 700;
    font-size: 1.5rem;
}

form ul {
    list-style-type: none;
    padding-left: 0;
}

form ul li {
    margin-bottom: 1rem;
}

label {
    display: block;
    padding-bottom: 1rem;
}

ul.radioGroup:first-child {
    padding-top: 1rem;
}

.radioGroup label,
.controlInlineContent label {
    display: inline;
}

input,
select,
textarea {
    border: 1px solid #000;
    padding: 15px;
    font-size: 1.1rem;
}

input,
select,
textarea {
    width: 99%;
}

textarea {
    height: 15rem;
}

input[type="radio"],
input[type="checkbox"] {
    width: auto;
}

input[type="radio"]+label,
input[type="checkbox"]+label {
    display: inline;
}

input:focus,
textarea:focus {
    background-color: var(--bg-light-color);
}

button {
    display: inline-block;
    width: auto;
    border-radius: 6px;
    margin-bottom: 15px;
    padding: 15px 30px;
    background-color: var(--bg-dark-color);
    color: #FFF;
    font-size: 1.1rem;
    border: none;
    text-transform: uppercase;
    appearance: none;
}

button:hover {
    background-color: var(--contrast-color);
}


/* Cuando tengamos como mínimo 768px en el viewport, interesará que los elementos con estas clases se muestre el uno al lado del otro y adapten su anchura*/
@media (min-width: 768px) {
    form {
        width: 85%;
        margin-left: auto;
        margin-right: auto;
    }

    li.controlInlineHalf {
        display: inline-block;
        width: 49%;
    }

    li.controlInlineHalf+li.controlInlineHalf {
        margin-left: 4px;
    }

    .half {
        width: 50%;
    }
}

/*categoria*/
.category {
    flex: 0 0 auto;
    margin-top: 40px;
    margin-bottom: 70px;
    margin: 0 auto;
}

.category h2 {
    margin-top: 40px;
    text-align: center;
}

.category ul {
    list-style: none;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}

.category li {
    flex: 0 0 auto;
    margin-bottom: 20px;
    padding-top: 20px;
    width: 160px;
    height: 160px;
    background-color: #f7f7f7;
    /* border: 1px solid #ddd; */
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-right: auto;
}

.category li:hover {
    background-color: #eee;
}

.category li img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    margin-bottom: 10px;
}

.category li h3 {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
}

.detail h1,
.related h2 {
    text-align: center;
}

.related h2 {
    padding-top: 30px;
}

.detail img {
    display: block;
    margin: 20px auto;

}

.related ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style-type: none;
    list-style: none;
    padding: 0;
}

.related li {
    margin: 10px;
    font-size: 1.2em;
    text-decoration: none;
    font-weight: bold;
    color: black;
}

.related li a {
    text-decoration: none;
    display: flex;
    align-items: center;
    color: #333;
}

.related li a:hover {
    color: red;
}

.relatedPark li a:before {
    content: "";
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-right: 5px;
    background-image: url("../img/treeicon.png");
    background-size: contain;
}

.relatedMuseum li a:before {
    content: "";
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-right: 5px;
    background-image: url("../img/museum.png");
    background-size: contain;
}

.relatedOther li a:before {
    content: "";
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-right: 5px;
    background-image: url("../img/places.png");
    background-size: contain;
}

.detail img {
    max-width: 60%;
    height: auto;
}

.related .actual {
    display: none;
}

.indice img,
.indice h2 {
    padding-bottom: 20px;
    text-align: center;
}

.indice img {
    display: block;
    width: 50%;
    margin: auto;
}




.form {
    padding: 30px 40px;
}

.form-control {
    position: relative;
}

.form-control label {
    display: block;
    margin-bottom: 5px;
}


.form-control input:focus {
    outline: 0;
    border-color: steelblue;
}

.form-control.success input,
.form-control.success textarea {
    border-color: var(--success-color);
}

.form-control.error input {
    border-color: var(--error-color);
}

.form-control small {
    color: var(--error-color);

    bottom: 0;
    left: 0;
    visibility: hidden;
}

.form-control.error small {
    visibility: visible;
}

.form button {
    cursor: pointer;
    border-radius: 4px;
    display: block;
    font-size: 16px;
    padding: 10px;
    margin-top: 20px;
    width: 100%;
}

.hidden {
    display: none;
}